'use strict';


// @ngInject
function ClipperContentController(
  $scope, frameSync,$location,$timeout,loader
) {
  var self = this;
  this.article = null;
  
  
  $scope.$on("SimplifiedArticle", function (event, article) {
	  if(article) {
		  $timeout(function() {
			  $scope.$apply(function() {
				  if(article) {
					  article.webClipName = article.title;
				  }
				  self.article = article;
				  loader.hideLoader();
			  }); 
		  }, 0);  
	  }
  });
}

module.exports = {
  controller: ClipperContentController,
  controllerAs: 'vm',
  bindings: {
    auth: '<'
  },
  template: require('../templates/clipper-content.html'),
};
