'use strict';
/**
 * This service runs in the clipper
 */
// @ngInject
function Loader($rootScope) {

  this.showLoader = function() {
	  $rootScope.$broadcast('loader', true);
  };
  
  this.hideLoader = function() {
	  $rootScope.$broadcast('loader', false);
  };

}

module.exports = Loader;
